
<template>
    <div>
        <mhead-top :titleHead="titleName?titleName:logo" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-shopping-cart-1 icon" rgUrl="/cart/cart"></mhead-top>
            <div class="mBgBox">
                <div class="tc w" v-if="product.length==0">
                    <img src="../../images/emptyCart.png" height="150" width="150" class="mt30"/>
                    <p class="f16 tc mt30">暂无数据</p>
                </div>
                <div class="mPBox" v-else>
                    <div  class="mItem" v-for="(item,index) in product" :key="index">
                        <router-link :to="{path:'/good/detail',query:{specifications_id:item.specifications_id}}">
                            <div class="tc">
                                <el-image class="mImg" :src="item.showPicIndex?item.upphotos[item.showPicIndex]:item.upphotos[0]" fit="cover"></el-image>
                            </div>
                            <div class="mSize">规格:{{item.specifications}}{{item.spec_unit}}</div>
                            <div class="mName wto">{{item.goods_name}}</div>
                            <div class="mPrice">{{item.unit_price}}<span class="f14">/{{item.supply_unit}}</span></div>
                        </router-link>
                    </div>
                </div>
            </div>
        <nav-foot menuActive="sort"></nav-foot>
    </div>
</template>

<script>
    import {categoryGoods} from '../../service/api'
    import mheadTop from '../../components/mobile/headTop';
    import navFoot from '../../components/mobile/navFoot';
    export default {
        name: 'mSortGood',
        data(){
            return{
                categoryId:'',
                titleName:'',
                product:[],
            }
        },
        components:{
            mheadTop,
            navFoot
        },
        mounted(){
            // 获取产品数据
            this.initData();

        },
        computed:{
        },
        methods:{
            async initData(){
                //获取菜单目录
                this.categoryId = this.$route.query.categoryId;
                this.titleName = this.$route.query.titleName;
                var res = await categoryGoods(this.categoryId);
                this.product = res;
            },
            showGoodDetail(item) {
                var _this = this;
            }
        },
        created(){

        },
    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    /*移动端*/
    .mBgBox{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mPBox{
        margin:px2rem(20);
        height: 100%;
        background-color: $bggray;
        display: flex;
        flex-wrap: wrap;
    }
    .mItem{
        background-color: #fff;
        border-radius:3px;
        width:px2rem(300);
        padding:px2rem(20);
        display: inline-block;
        margin-bottom:10px;
    }
    .mItem:nth-child(even){
        margin-left:px2rem(20) ;
    }
    .mImg{
        width:px2rem(300);
        height:px2rem(250);
    }
    .mSize{
        font-size:px2rem(22);
        color:#999;
    }
    .mName{
        font-size:px2rem(28);
        color:#333;
    }
    .mPrice{
        font-size:px2rem(26);
        color:$colorprice;
    }
    .mAdv{
        margin:px2rem(20) px2rem(20) 0 px2rem(20);

    }
    .mBanner{
        width:100%;
        display: block;
    }
    .mAdvPro{
        margin:px2rem(20) px2rem(20) 0 px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .mPro{
        width:px2rem(345);
    }



</style>



